import React from 'react'

const Duration = ({title, active}) => {
  return (
  <>
    {active && <div className='Duration' style={{color: active ? 'white' : '#AEB8C4', backgroundColor: active ? '#AEB8C4' : 'transparent'}}>{title}</div>}
    {!active && <div className='Duration'>{title}</div>}
  </>
    
  )
}

export default Duration