import { Area, AreaChart, CartesianGrid, Legend, Line, LineChart, ResponsiveContainer, Tooltip, XAxis, YAxis } from 'recharts';

const RechartsExample = () => {

    const gradientColor = 'linear-gradient(to bottom, #8884d8, #ebedf0)';
    const data = [
        {price: 31},
        {price: 45},
        {price: 43},
        {price: 49},
        {price: 46},
        {price: 48},
        
    ]




    return ( 
        <div className='Cards' style={{padding: '0'}} >
            <div className="price-info">
                <div className="lower">
                    <p>Lower: $4.895</p>
                </div>
                <div className="higher">
                    <p>Higher: $6.857</p>
                </div>
            </div>

            <ResponsiveContainer width="100%" >

                <AreaChart  width="100%"  height={195} data={data} margin={{ top: 70, right: 30, left: 0, bottom: 0 }} className='chart'>
                    <Area type="monotone" dataKey="price" stroke="#FA8E1F"
                    strokeWidth={3} str fillOpacity={1} fill="#FFF6ED"/>
                </AreaChart>

            </ResponsiveContainer>
            
            
        </div>
     );
}
 
export default RechartsExample;